import React from "react";
import NavBar from "../components/Panels/NavBar";
import Footer from "../components/Panels/Footer";
import Wrapper from "../components/Panels/Wrapper";
import AddPatientsBtn from "../components/Buttons/AddPatientsBtn";
import AddPracticionersBtn from "../components/Buttons/AddPracticionersBtn";
import AddProceduresBtn from "../components/Buttons/AddProceduresBtn";
import AddInventoryBtn from "../components/Buttons/AddInventoryBtn";
import ViewPatientsBtn from "../components/Buttons/ViewPatientsBtn";
import ViewPracticionersBtn from "../components/Buttons/ViewPracticionersBtn";
import ViewProceduresBtn from "../components/Buttons/ViewProceduresBtn";
import ViewInventoryBtn from "../components/Buttons/ViewInventoryBtn";
import ReportsBtn from "../components/Buttons/ReportsBtn";
import { Grid, Row, Col } from "react-bootstrap";
import "./style.css";

const Menu = props => {
  return (
    <Wrapper>
       <NavBar />
      <Grid>
        <Row className="container">
          <Col xs={12} sm={12} md={4} lg={4}>
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
              <AddPatientsBtn/>
              </Col>
              <Col xs={12} sm={12} md={6} lg={6}>
              <ViewPatientsBtn/>
              </Col>
            </Row>
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
              <AddPracticionersBtn/>
              </Col>
              <Col xs={12} sm={12} md={6} lg={6}>
              <ViewPracticionersBtn/>
              </Col>
            </Row>
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
              <AddProceduresBtn/>
              </Col>
              <Col xs={12} sm={12} md={6} lg={6}>
              <ViewProceduresBtn/>
              </Col>
            </Row>
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
              <AddInventoryBtn/>
              </Col>
              <Col xs={12} sm={12} md={6} lg={6}>
              <ViewInventoryBtn/>
              </Col>
            </Row>
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
              <ReportsBtn/>
              </Col>
            </Row>
          </Col>
        </Row>
      </Grid>
      <Footer />
    </Wrapper>
  );
};

export default Menu;


